@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.6/dist/web/static/pretendard.css");
* {
    text-decoration: none;
    font-family: 'pretendard';
}
/* header */
.headerWrap {
    display: flex;
    justify-content: center;
    border-bottom: 1px solid #e3e3e3;
    background-color: #fff;
}
.header {
    flex: 0 0 1000px;
}
h1 {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}
.logo {
    display: inline-block;
    flex: 1 1 0;
    width: 102px;
    height: 32px;
    color: #ff385c
}
.header input {
    height: 36px;
    flex: 1 1 0;
    padding: 10px;
    border: 1px solid #eeeeee;
    border-radius: 20px;
    box-shadow: 0 0 5px 0px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
}
.header input:hover {
    box-shadow: 1px 1px 6px 0px rgba(0, 0, 0, 0.2);
}
.header .headerRight {
    display:flex;
    justify-content: flex-end;
    flex: 2 1 0;
}
.header .headerText {
    display: inline-block;
    height: 36px;
    padding: 10px 25px;
    color: #3c3c3c;
    text-align: center;
    border-radius: 20px;
    font-size: 13px;
    font-weight: lighter;
    box-sizing: border-box;
}
.header .headerText:hover {
    background-color: #f8f8f8;
}
.header .language {
    display: flex; 
    justify-content: center; 
    align-items: center;
    width: 36px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    border-radius: 20px;
    cursor: pointer;
}
.header .language:hover {
    background-color: #f8f8f8;
}
.header .myPage {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 36px;
    padding: 0 15px;
    margin-left: 10px;
    box-sizing: border-box;
    border: 1px solid #eeeeee;
    border-radius: 20px;
}
.header .myPage:hover {
    box-shadow: 1px 1px 6px 0px rgba(0, 0, 0, 0.2);
}
.header .myPage svg {
    cursor: pointer;
}

/* contents */
.contentsWrap {
    display: flex;
    flex-direction: column;
}
.tabWrap, .itemWrap {
    display: flex;
    justify-content: center;
}
.tabAll {
    display: flex;
    justify-content: space-between;
    flex: 0 0 1000px;
    height: 55px;
    margin-top: 20px;
}
.tabAll .tabList:nth-child(1) { background: url('../public/images/mainTab/0.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(2) { background: url('../public/images/mainTab/1.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(3) { background: url('../public/images/mainTab/2.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(4) { background: url('../public/images/mainTab/3.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(5) { background: url('../public/images/mainTab/4.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(6) { background: url('../public/images/mainTab/5.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(7) { background: url('../public/images/mainTab/6.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(8) { background: url('../public/images/mainTab/7.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(9) { background: url('../public/images/mainTab/8.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(10) { background: url('../public/images/mainTab/9.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(11) { background: url('../public/images/mainTab/10.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(12) { background: url('../public/images/mainTab/11.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(13) { background: url('../public/images/mainTab/12.jpg') no-repeat top center / 55%; }
.tabAll .tabList:nth-child(14) { background: url('../public/images/mainTab/13.jpg') no-repeat top center / 55%; }
.itemAll {
    display: flex;
    justify-content: space-between;
    flex: 0 0 1016px;
    margin-left: 15px;
    flex-wrap: wrap;
    margin-top: 20px;
    margin-left: 8px;
}
.itemAll .item {
    display: inline-block;
    color: #333;
    font-size: 13px;
    margin-right: 16px;
    background-size: 100%;
}
.itemAll .item:hover {
    cursor: pointer;
}
.itemAll .item:hover div {
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.3);
}
.itemAll .item span {
    display: inline-block;
    width: 100%;
    font-size: 12px;
    color:#a3a3a3;
}
.itemAll .item span:nth-child(2) {
    margin: 10px 0 5px 0;
    font-size: 14px;
    color: #333;
}
.itemAll .item span:last-child {
    color: #333;
}